import React, { useState, useEffect } from 'react';

const translations = {
  tr: {
    prayerTimes: "Namaz Vakitleri",
    fajr: "msak",
    dhuhr: "le",
    asr: "kindi",
    maghrib: "Akam",
    isha: "Yats",
    qibla: "Kble Yn"
  },
  ar: {
    prayerTimes: " ",
    fajr: "",
    dhuhr: "",
    asr: "",
    maghrib: "",
    isha: "",
    qibla: " "
  },
  fr: {
    prayerTimes: "Heures de prire",
    fajr: "Fajr",
    dhuhr: "Dhuhr",
    asr: "Asr",
    maghrib: "Maghrib",
    isha: "Isha",
    qibla: "Direction de la Qibla"
  },
  en: {
    prayerTimes: "Prayer Times",
    fajr: "Fajr",
    dhuhr: "Dhuhr",
    asr: "Asr",
    maghrib: "Maghrib",
    isha: "Isha",
    qibla: "Qibla Direction"
  },
  ru: {
    prayerTimes: " ",
    fajr: "",
    dhuhr: "",
    asr: "",
    maghrib: "",
    isha: "",
    qibla: " "
  },
  zh: {
    prayerTimes: "",
    fajr: "",
    dhuhr: "",
    asr: "",
    maghrib: "",
    isha: "",
    qibla: ""
  },
  de: {
    prayerTimes: "Gebetszeiten",
    fajr: "Fajr",
    dhuhr: "Dhuhr",
    asr: "Asr",
    maghrib: "Maghrib",
    isha: "Isha",
    qibla: "Qibla-Richtung"
  },
  kk: {
    prayerTimes: " ",
    fajr: "",
    dhuhr: "",
    asr: "",
    maghrib: "",
    isha: "",
    qibla: " "
  },
  az: {
    prayerTimes: "Namaz Vakitleri",
    fajr: "Sbh",
    dhuhr: "Zhr",
    asr: "sr",
    maghrib: "Mrib",
    isha: "a",
    qibla: "Qibl Yn"
  }
};

const PrayerTimes = () => {
  const [language, setLanguage] = useState('en');
  const [prayerTimes, setPrayerTimes] = useState({});
  const [currentTime, setCurrentTime] = useState(new Date());
  const [qiblaDirection, setQiblaDirection] = useState(0);

  useEffect(() => {
    const selectedLanguage = localStorage.getItem('selectedLanguage') || 'en';
    setLanguage(selectedLanguage);
  }, []);

  useEffect(() => {
    fetchPrayerTimes();
    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 60000);
    return () => clearInterval(timer);
  }, []);

  useEffect(() => {
    const handleOrientation = (event) => {
      const alpha = event.alpha;
      setQiblaDirection(alpha);
    };
    window.addEventListener('deviceorientation', handleOrientation);

    return () => window.removeEventListener('deviceorientation', handleOrientation);
  }, []);

  const fetchPrayerTimes = async () => {
    try {
      const response = await fetch(`https://api.aladhan.com/v1/timings?latitude=YOUR_LATITUDE&longitude=YOUR_LONGITUDE&method=2`);
      const data = await response.json();
      setPrayerTimes(data.data.timings);
    } catch (error) {
      console.error("Namaz vakitleri alnrken hata olutu:", error);
    }
  };

  const getNearestPrayer = () => {
    const times = Object.entries(prayerTimes).map(([key, value]) => ({
      name: key,
      time: new Date(currentTime.toDateString() + ' ' + value)
    }));
    const now = new Date();
    const nearest = times.reduce((prev, curr) => (curr.time > now && curr.time < prev.time) ? curr : prev, { time: new Date(now.getTime() + 24 * 60 * 60 * 1000) });
    return nearest.name;
  };

  const nearestPrayer = getNearestPrayer();

  const styles = {
    container: {
      width: '100%',
      height: '100vh',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: '#f0f0f0',
      fontFamily: 'Arial, sans-serif',
    },
    prayerTime: {
      fontSize: 24,
      fontWeight: 'bold',
      margin: 10,
    },
    highlighted: {
      fontSize: 24,
      fontWeight: 'bold',
      margin: 10,
      backgroundColor: '#FFD700',
      padding: '10px',
      borderRadius: '8px',
    },
    qiblaContainer: {
      marginTop: 30,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    qiblaImage: {
      width: 200,
      height: 200,
      transform: `rotate(${qiblaDirection}deg)`,
    },
    qiblaText: {
      marginTop: 10,
      fontSize: 18,
      fontWeight: 'bold',
    },
  };

  return (
    <div style={styles.container}>
      <div style={styles.prayerTime}>
        {translations[language]?.prayerTimes || "Prayer Times"}
      </div>
      {Object.entries(prayerTimes).map(([key, value]) => (
        <div
          key={key}
          style={key.toLowerCase() === nearestPrayer ? styles.highlighted : styles.prayerTime}
        >
          {translations[language]?.[key.toLowerCase()] || key}: {value}
        </div>
      ))}
      <div style={styles.qiblaContainer}>
        <img src="/qibla-compass.png" alt="Qibla Compass" style={styles.qiblaImage} />
        <div style={styles.qiblaText}>
          {translations[language]?.qibla || "Qibla Direction"}
        </div>
      </div>
    </div>
  );
};

export default PrayerTimes;
